.body {
    display: flex;
    flex-flow: row wrap;
}

.card {
    flex: 1 1 46%;
    border: 1px solid #ccc;
    box-sizing: border-box;
    padding: 20px;
    margin: 15px;
    box-shadow: 0px 2px 4px 0px rgb(167, 167, 167);
}

.poster img {
    width: 100%;
    height: auto;
}

.header {
    font-weight: bold;
}